<template>
	<div class="mb-3">
		<p id="header">{{ item.name }}</p>
		<v-radio-group hide-details class="mt-0 options">
			<v-radio v-for="option in item.options"
				:ripple="false"
				:label="formatLabel(option)"
				:value="option.name" />
		</v-radio-group>
	</div>
</template>

<script>
export default {
	data() {
		return {
			item: this.$store.state.menu['Mains'][0].compulsory_options[0],
		};
	},

	methods: {
		formatLabel(option) {
			return `${ option.name } (+$${ option.cost.toFixed(2) })`;
		},
	},
};
</script>

<style>
.options.v-input .v-label {
	text-transform: capitalize;
}
</style>

<style scoped>
#header {
	margin: 0px;
	font-size: 18px;
}
</style>