<template>
	<div class="mb-3">
		<p id="header">{{ item.name }}</p>
		<v-checkbox v-for="option in item.options"
			hide-details :ripple="false"
			class="options mt-0"
			:label="formatLabel(option)" />
	</div>
</template>

<script>
export default {
	data() {
		return {
			item: this.$store.state.menu['Mains'][0].optional_options[0],
		};
	},

	methods: {
		formatLabel(option) {
			return `${ option.name } (+$${ option.cost.toFixed(2) })`;
		},
	},
};
</script>

<style>
.options.v-input .v-label {
	text-transform: capitalize;
}
</style>

<style scoped>
#header {
	margin: 0px;
	font-size: 18px;
}
</style>