<template>
	<div id="dialog-quantity">
	  <v-btn flat icon outline small>
	    <v-icon small @click="decrease">remove</v-icon>
	  </v-btn>

	  <v-text-field hide-details class="pa-0 text-center"
	  	v-model="quantity"
	  	:mask="mask" />

	  <v-btn flat icon outline small>
	    <v-icon small @click="increase">add</v-icon>
	  </v-btn>
	</div>
</template>

<script>
export default {
	data() {
		return {
			mask: '##',
			quantity: 2,
		};
	},

	methods: {
		increase() {
			if (typeof this.quantity !== 'number') this.quantity = 0;
			if (this.quantity < 99) this.quantity += 1;
		},
		decrease() {
			if (typeof this.quantity !== 'number') this.quantity = 0;
			if (this.quantity > 0) this.quantity -= 1;
		},
	},
};
</script>

<style>
.v-input input {
	text-align: center;
	font-weight: bold;
}
</style>

<style lang="scss" scoped>
