<template>
  <v-list-tile>
    <v-list-tile-content>
      <v-list-tile-title>{{ item.name }}</v-list-tile-title>
      <v-list-tile-sub-title>{{ item.description || 'Delicious!' }}</v-list-tile-sub-title>
    </v-list-tile-content>

    <v-list-tile-avatar>
      {{ formatted.price }}
    </v-list-tile-avatar>
  </v-list-tile>
</template>

<script>
export default {
  props: ['item'],
  computed: {
    formatted() {
      return {
        price: `$${ parseFloat(this.item.base_price).toFixed(2) }`,
      };
    },
  },
};
</script>